@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imageContainer {
      width: 700px;
      height: 700px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      // float: right;

      // @include mobile {
      //   align-items: flex-start;
      // }

      img {
        height: 90%;

        @include mobile {
          height: 70%;
        }
      }
    }
  }

  .right {
    flex: 0.5;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile {
        padding-left: 0;
        align-items: center;
      }

      h1 {
        font-size: 60px;
        margin: 10px 0;

        @include mobile {
          font-size: 30px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 30px;

        @include mobile {
          font-size: 20px;
        }

        span {
          font-size: inherit;
          color: burlywood;
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 30%;
    }
    .icon {
      font-size: 60px;
      color: black;
      animation: arrowBlink 2s infinite; //화살표 2초뒤에 불투명하게 만들기
    }

    @keyframes arrowBlink {
      100% {
        opacity: 0;
      }
    }
  }
}
