@import "../../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 50px;

    @include mobile {
      font-size: 20px;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px; //4개 2개에서 3개 3개로 바뀜
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      color: white; //글자가 흰색으로
      transition: all 0.5s ease;
      cursor: pointer;

      h3 {
        position: absolute; //그림안에 글자가 들어가게함
        font-size: 20px;
      }

      img {
        width: 100%;
        height: 100%;

        object-fit: cover;
        z-index: 1; //글씨 안보이게
      }
      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
