@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    margin: 20px;
    font-size: 20px;

    @include mobile {
      font-size: 20px;
      margin: 7px;
    }
  }

  ul {
    margin: 7px;
    font-size: 15px;
  }

  .itemContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;

    .github {
      font-size: 25px;
      margin: 5px;
      cursor: pointer;
    }

    .blog {
      font-size: 27px;
      margin: 5px;
      cursor: pointer;
    }
  }
}
